<!--  -->
<template>
    <div id="running-helper" class='running-helper' style="position:absolute;display: none;
    left: 77%;top: 1%; z-index: 100;">
        <el-card class="box-card">
            <div slot="header" class="clearfix">
                <span>{{title}}</span>
                <el-button style="float: right; padding: 3px 0" type="text" @click="onClose">关闭</el-button>
            </div>
            <div class="text item">
                <template v-if="!isSubmit">
                    <el-form ref="running_form" :model="RunningForm" label-width="80px">
                        <el-form-item label="起点" prop="origin">
                            <el-autocomplete
                            class="inline-input"
                            v-model="RunningForm.origin"
                            :fetch-suggestions="querySearch"
                            placeholder="请输入起点"
                            @select="handleSelect"
                            ></el-autocomplete>
                            <!-- <el-input v-model="RunningForm.origin" placeholder="请输入起点"></el-input> -->
                        </el-form-item>
                        <el-form-item label="终点" prop="target">
                            <el-autocomplete
                            class="inline-input"
                            v-model="RunningForm.target"
                            :fetch-suggestions="querySearch"
                            placeholder="请输入起点"
                            @select="handleSelect"
                            ></el-autocomplete>
                            <!-- <el-input v-model="RunningForm.target" placeholder="请输入终点"></el-input> -->
                        </el-form-item>
                        <el-form-item label="公里数" prop="num">
                            <el-input-number v-model="RunningForm.num" :step="0.5"></el-input-number>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="onSubmit">路径规划</el-button>
                        </el-form-item>
                    </el-form>
                </template>
                <template v-else>
                    <el-table
                    height="300"
                    :data="resultTable"
                    style="width: 100%">
                        <el-table-column
                            prop="place"
                            label="地点"
                            width="250">
                        </el-table-column>
                        <el-table-column
                            prop="signUp"
                            label="打卡点"
                            width="110">
                        </el-table-column>
                    </el-table>
                </template>
            </div>
        </el-card>
    </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';

export default {
//import引入的组件需要注入到对象中才能使用
components: {},
data() {
//这里存放数据
return {
    selectList:[],

    title:'乐跑助手',
    isSubmit:false,
    RunningForm:{
        origin:'',
        target:'',
        num:1.5
    },

    resultTable:[
        {
            place:'地点1',
            signUp:'一号打卡点'
        },
        {
            place:'地点2',
            signUp:''
        },
        {
            place:'地点3',
            signUp:''
        },
        {
            place:'地点4',
            signUp:''
        },
        {
            place:'地点5',
            signUp:'二号打卡点'
        },
    ]
};
},
//监听属性 类似于data概念
computed: {},
//监控data中的数据变化
watch: {},
//方法集合
methods: {
    onShow(){
        this.refresh();
        document.getElementById('running-helper').style.display = 'block';
    },
    onClose(){
        this.title = '乐跑助手'
        this.isSubmit = false;
        document.getElementById('running-helper').style.display = 'none';
    },
    onSubmit(){
        this.title = '乐跑助手：规划结果'
        this.isSubmit = true;
    },
    refresh(){
        this.RunningForm.origin = '',
        this.RunningForm.target = '',
        this.RunningForm.num = 1.5
    },

    querySearch(queryString, cb) {
        var list = this.selectList;
        var results = queryString ? list.filter(this.createFilter(queryString)) : list;
        // 调用 callback 返回建议列表的数据
        cb(results);
    },
    createFilter(queryString) {
        return (list) => {
          return (list.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
        };
    },
    loadAll(){
        // console.log(this.$store.state.placeList)
        return this.$store.state.placeList
    },

    handleSelect(item){
        console.log(item)
    }
},
//生命周期 - 创建完成（可以访问当前this实例）
created() {

},
//生命周期 - 挂载完成（可以访问DOM元素）
mounted() {
    this.selectList = this.loadAll();
},
beforeCreate() {}, //生命周期 - 创建之前
beforeMount() {}, //生命周期 - 挂载之前
beforeUpdate() {}, //生命周期 - 更新之前
updated() {}, //生命周期 - 更新之后
beforeDestroy() {}, //生命周期 - 销毁之前
destroyed() {}, //生命周期 - 销毁完成
activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
<style scoped>
    .text {
        font-size: 14px;
    }

    .item {
        margin-bottom: 18px;
    }

    .clearfix:before,
    .clearfix:after {
        display: table;
        content: "";
    }
    .clearfix:after {
        clear: both
    }

    .box-card {
        width: 400px;
    }


</style>


