<!--手机浏览器模式下未来之路导航系统  -->
<template>
  <div class='mobile-container'>
    <el-header style="background-color:#409EFF;">
      <div class="mobile-header__wrap">
        <span style="font-size:24px; color:white;font-family:'common-Regular'">未来之路</span>
        <!-- <span style="font-size:16px;color:white;font-family:'common-Regular'">一站式校园助手</span> -->
      </div>
    </el-header>
    <div id="antvMap" style="width:100%;height:100%;position: absolute; z-index: 0;"></div>
    <ul class="buttonMenu">
      <li :class="{'activeButton':activeModel===1}" @click="touchModel(1)">
        <i class="el-icon-menu"></i>
        <div>乐跑助手</div>
      </li>
      <li class="centerButton" :class="{'activeButton':activeModel===2}" @click="touchModel(2)">
        <i class="el-icon-menu"></i>
        <div>路径指导</div>
      </li>
      <li :class="{'activeButton':activeModel===3}" @click="touchModel(3)">
        <i class="el-icon-menu"></i>
        <div>日程规划</div>
      </li>
    </ul>
    <MRunningHelp :drawer="activeModel===1" />
    <MRoutePlan :drawer="activeModel===2" />
    <MDateLog :drawer="activeModel===3" />
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import { AntvMap } from "../../static/Plugins/Script/antvMap.js";
import MRoutePlan from "../components/MRoutePlan.vue";
import MDateLog from "../components/MDateLog.vue";
import MRunningHelp from "../components/MRunningHelper.vue";

export default {
  //import引入的组件需要注入到对象中才能使用
  components: {
    MRoutePlan,
    MDateLog,
    MRunningHelp,
  },
  data() {
    //这里存放数据
    return {
      activeModel: 0,
      AntvMapInstance: null,
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //方法集合
  methods: {
    init() {
      this.AntvMapInstance = new AntvMap("antvMap");
      this.AntvMapInstance.loadMap();
    },
    // loadMap() {
    //   console.log("当前使用设备");
    //   console.log(
    //     navigator.userAgent.match("Win") ? "windows操作系统" : "其他操作系统"
    //   );
    //   LoadMap("antvMap");
    // },
    touchModel(value) {
      this.activeModel = value;
    },
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {},
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {
    // this.loadMap();
    this.init();
  },
  //生命周期 - 创建之前
  beforeCreate() {},
  //生命周期 - 挂载之前
  beforeMount() {},
  //生命周期 - 更新之前
  beforeUpdate() {},
  //生命周期 - 更新之后
  updated() {},
  //生命周期 - 销毁之前
  beforeDestroy() {},
  //生命周期 - 销毁完成
  destroyed() {},
  //如果页面有keep-alive缓存功能，这个函数会触发
  activated() {},
};
</script>
<style scoped>
.mobile-container {
  width: 100%;
  height: 100%;
}

.mobile-header__wrap {
  display: flex;
  margin-top: 4.5%;
  padding: 15px;
  justify-content: center;
}

.buttonMenu {
  position: absolute;
  bottom: 0;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 50px;
  text-align: center;
  background-color: #fff;
  border-radius: 10px 10px 0 0;
}

.buttonMenu li {
  display: inline-block;
  list-style-type: none;
}

.centerButton {
  margin: 0 35px;
}
.activeButton {
  color: rgba(0, 162, 255, 0.974);
}
</style>


