<!--  -->
<template>
  <div id="ropute-plan" class='navigator-card' style="position:absolute;display: none;
left: 75%;top: 1%; z-index: 100;">
    <el-card class="box-card" shadow="always">
      <div slot="header" class="clearfix">
        <span>目的地导航</span>
        <el-button style="float: right; padding: 3px 0" type="text" @click="onClose">关闭</el-button>
      </div>
      <div class="text item">
        <template v-if="!isSubmit">
          <el-row>
            <el-autocomplete class="inline-input" v-model="navigatorForm.origin" :fetch-suggestions="querySearch" :placeholder="originText" @select="handleSelect"></el-autocomplete>
            <el-autocomplete class="inline-input" v-model="navigatorForm.target" :fetch-suggestions="querySearch" :placeholder="targetText" @select="handleSelect"></el-autocomplete>
          </el-row>
          <el-row style="margin-top:15px;">
            <el-select v-model="PathOptions.value" placeholder="请选择" style="width:202px;">
              <el-option v-for="item in PathOptions.options" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
            <el-button type="primary" @click="onSubmit">立即导航</el-button>
          </el-row>
          <el-row>
            <el-table :data="ResultPathList" style="width: 100%" height="300">
              <el-table-column label="地点" width="360">
                <template slot-scope="scope">
                  <i class="el-icon-s-promotion"></i>
                  <span style="margin-left: 10px">{{ scope.row.place }}</span>
                </template>
              </el-table-column>
            </el-table>
          </el-row>
        </template>
      </div>
    </el-card>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';

let pointer = null;

export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data() {
    //这里存放数据
    return {
      originText: "请指定出发点",
      targetText: "请指定目的地",

      selectPathList: [],

      isSubmit: false,

      originText: "请指定出发点",
      targetText: "请指定目的地",

      navigatorForm: {
        origin: "",
        target: "",
        isWalk: true,
        isSignUp: true,
        isParkNeed: true,
      },

      roles: {
        origin: [{ required: true, message: "请输入起始点", trigger: "blur" }],
        target: [{ required: true, message: "请输入目的地", trigger: "blur" }],
      },

      PathOptions: {
        options: [],
        value: "",
      },

      ResultPathList: [
        {
          place: "地点1",
        },
        {
          place: "地点2",
        },
        {
          place: "地点3",
        },
        {
          place: "地点4",
        },
        {
          place: "地点5",
        },
      ],
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //方法集合
  methods: {
    init() {
      pointer = this;
    },

    querySearch(queryString, cb) {
      var list = this.selectPathList;
      var results = queryString
        ? list.filter(this.createFilter(queryString))
        : list;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    createFilter(queryString) {
      return (list) => {
        return (
          list.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0
        );
      };
    },
    loadAll() {
      // console.log(this.$store.state.placeList);
      return this.$store.state.placeList;
    },

    // 清空当前组件的所有数据
    refresh() {
      this.navigatorForm.origin = "";
      this.navigatorForm.target = "";

      // 获取定位，并自动填充到出发点输入框
      if (this.$store.state.auto_orient) {
        this.originText = "正在定位...";
        this.getLocation();
      } else {
        this.originText = "请指定出发点";
      }
    },

    getLocation() {
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(
          (data) => {
            // todo 获取到定位信息后实现
            console.log(data);
          },
          (error) => {
            console.log(error);
            // 定位信息获取失败
            pointer.origin = "定位失败";
          },
          {
            enableHighAccuracy: true,
            timeout: 3000,
          }
        );
      } else {
        this.$confirm(
          "请在浏览器设置中开放定位权限，或者在设置中关闭'自动定位我的位置'选项",
          "未能获取定位权限",
          {
            confirmButtonText: "好的",
            type: "error",
            center: true,
          }
        );
      }
    },

    onSubmit() {
      this.$refs["form"].validate((valid) => {
        if (valid) {
          // todo
          this.$message("功能建设中，敬请期待~");
        } else {
          // throw error
        }
      });
    },

    onShow() {
      this.refresh();
      document.getElementById("ropute-plan").style.display = "block";
    },

    onSubmit() {
      this.$refs["form"].validate((valid) => {
        if (valid) {
          // todo
          this.isSubmit = true;
          // this.$message('功能建设中，敬请期待~');
        } else {
          // throw error
        }
      });
    },

    onShow() {
      this.refresh();
      document.getElementById("ropute-plan").style.display = "block";
    },

    onClose() {
      this.isSubmit = false;
      document.getElementById("ropute-plan").style.display = "none";
    },

    back() {
      this.isSubmit = false;
    },

    handleSelect(item) {
      console.log(item);
    },
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {},
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {
    this.selectPathList = this.loadAll();
  },
  beforeCreate() {}, //生命周期 - 创建之前
  beforeMount() {}, //生命周期 - 挂载之前
  beforeUpdate() {}, //生命周期 - 更新之前
  updated() {}, //生命周期 - 更新之后
  beforeDestroy() {}, //生命周期 - 销毁之前
  destroyed() {}, //生命周期 - 销毁完成
  activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
};
</script>
<style scoped>
/* 卡片框 */
.text {
  font-size: 14px;
}

.item {
  margin-bottom: 16px;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}

.box-card {
  width: 450px;
}
</style>


